import React, { useEffect, useState } from 'react'
import CheckBoxList from './CheckBoxList'
import TabInputTab from './TabInputTab'
import { useDispatch, useSelector } from 'react-redux';
import TabAccountList from './TabAccountList';
import TabAccountPicker from '../../../AddFeed/component/TabAccountPicker';
import { SET_SELECTED_ACCOUNT } from '../../../../../../../actions/addFeedActions';
import { useParams } from 'react-router-dom';
import AddAccountBlock from '../../component/AddAccountBlock';
import { GET_NETWORK_DATA_BY_ID } from '../../../../../../../utils';
import TwitterAdvanceTable from '../../../AddFeed/component/twitterAdvanceTable';
import { Row, Col } from 'react-bootstrap';
import EmptyWithIcon from '../../../../CommonComponents/Empty/EmptyWithIcon';
import { updateSalesModal } from '../../../../../../../actions/loginActions';
import { HUBSPOT_URL } from '../../../../../../../actions/constants';
import { MediaBaseUrl } from '../../../../../../../actions/api';

const RenderBody = ({ filterData, accounts, activeTab }) => {
    const { networkId } = useParams()

    const filters = [26, 23, 71, 72, 70, 2, 7, 3, 4, 5, 8, 33, 75, 11, 12, 56, 67, 32, 28, 27, 78, 80, 77, 84, 83, 22, 13, 16]

    // ----------check for input field | excluded instagram personal tab with filter id = 1 which is common---------
    if (filters.includes(filterData.filter) || (filterData.filter == 1 && networkId != 18) || (filterData.filter == 30 && networkId == 18)) {
        return <Row>
            {networkId == 20 ? <Col sm={6}>
                <TabAccountList accounts={accounts} activeTab={activeTab} filterData={filterData} />
            </Col> : null}
            <TabInputTab filterData={filterData} accounts={accounts} activeTab={activeTab} />{networkId == 12 ? <span className='d-flex justify-content-end'><a href={`${MediaBaseUrl}/media/downloads/sample-xml.txt`} download="sample-xml.txt">Download Sample</a></span> : null}</Row>;
    }

    // ----------for account list --------------------
    else return <div className='mb-4'><TabAccountList accounts={accounts} activeTab={activeTab} filterData={filterData} /></div>
}

const TabBody = ({ filterData, accounts, networkDetails, activeTab }) => {
    const { networkId } = useParams()
    const dispatch = useDispatch()
    const showAccountPicker = () => {
        const filters = [26, 23, 71, 72, 70, 2, 7, 3, 4, 5, 8, 75, 11, 12, 56, 67];
        if (networkId != 20 && (filters.includes(filterData.filter) || (filterData.filter == 1 && networkId != 18))) return true
        return false
    }

    useEffect(() => {
        // ---------------set default account------------
        if (accounts?.length) dispatch(SET_SELECTED_ACCOUNT(accounts[0]))

        return () => {
            if (accounts?.length) dispatch(SET_SELECTED_ACCOUNT(accounts[0]))
        }
    }, [accounts, dispatch]);

    return ( 
        (networkId == 28 && filterData.filter == 1) ? <AddAccountBlock network={GET_NETWORK_DATA_BY_ID(networkId)} activeTab={activeTab} />
            :
            (networkId == 3 && filterData.filter == 2) ?
                <div className="d-flex flex-column align-items-center h-100 justify-content-center dsb-empty">
                    <EmptyWithIcon title="This Feature is on Request! Contact us to embed Facebook Hashtags on website." desc={''} icon={''} button={{ action: () => dispatch(updateSalesModal({ url: `${HUBSPOT_URL}`, embedCode: null })), text: 'Talk to Sales' }} isFullScreen={false} />
                </div> : <>
                    {/* ----------render input/account list for tab-------- */}
                    <RenderBody accounts={accounts} filterData={filterData} activeTab={activeTab} />
                    {filterData.filter == 5 && networkId == 1 ? <TwitterAdvanceTable /> : <></>}

                    <div className='mb-4'>
                        <CheckBoxList filterData={filterData} />
                    </div>
                    {showAccountPicker() ? <div className='mb-4'><TabAccountPicker accounts={accounts} /></div> : <></>}
                </>
    )
}

export default TabBody