import { SET_AUTOCOMPLETE_HANDLE, SET_AUTOCOMPLETE_LIST, SET_ERROR_MESSAGE_ACITON, SET_FEED_OBJECT, YOUTUBE_AUTOCOMPLETE } from "../../../../../actions/addFeedActions";
import { AUTOCOMPLETE } from "../../../../../actions/manageFeedsAction";
import store from "../../../../../store";
import { isPersInstaAcc, URL_VALIDATION } from "../../../../../utils";

export const GET_CHILD_ACCOUNTS = (accounts, networkId, filter = "") => {
    if (networkId == 10 || networkId == 20 || networkId == 4 || networkId == 3 || (networkId == 18 && !isPersInstaAcc(parseInt(filter)))) {
        return accounts?.reduce((acc, account) => {
            if (account.ConnectedAccounts.child) {
                acc = [...acc, ...account.ConnectedAccounts.child];
            }
            return acc;
        }, []) || [];
    }
    return accounts
}

export const AUTOCOMPLETE_INPUT = (value, networkId, filter = "", latlng = "") => (dispatch) => {
    switch (parseInt(networkId)) {
        case 3:
            dispatch(FB_AUTOCOMPLETE(value))
            break;

        case 1:
            dispatch(TWITTER_AUTOCOMPLETE(value))
            break;

        case 7:
            dispatch(YT_CHANNEL_AUTOCOMPLETE(value, filter))
            break;

        case 19: 
            dispatch(YELP_AUTOCOMPLETE(value, latlng));
            break;

        default:
            break;
    }
}

const FB_AUTOCOMPLETE = (value) => (dispatch) => {
    const obj = { networkId: 3, handle: value, filterType: 8, showHandle: false }
    AUTOCOMPLETE(obj).then(({ data }) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE(data.responseCode === 200 ? data.response.data : []))
    }).catch((error) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE([]))
    })
}

const TWITTER_AUTOCOMPLETE = (value) => (dispatch) => {
    const obj = { networkId: 1, handle: value }
    AUTOCOMPLETE(obj).then(({ data }) => {
        if (data.responseCode === 200) {
            const { responseData, responseDataUserList } = data;
            dispatch(SET_AUTOCOMPLETE_HANDLE(responseData.data));
            dispatch(SET_AUTOCOMPLETE_LIST(responseDataUserList?.data));
        } else {
            dispatch(SET_AUTOCOMPLETE_HANDLE([]));
            dispatch(SET_AUTOCOMPLETE_LIST([]));
        }
    });
}

const YT_CHANNEL_AUTOCOMPLETE = (value, filter) => (dispatch) => {
    if ([11, 1, 75].includes(parseInt(filter))) {
        const type = filter == 11 ? "playlist" : "channel"
        YOUTUBE_AUTOCOMPLETE(value, type).then(res => {
            const { data, status } = res
            dispatch(SET_AUTOCOMPLETE_HANDLE(status === 200 ? data.items : []))
        })
    }
    else return
}

const YELP_AUTOCOMPLETE = (value, latlng) => (dispatch) => {
    const obj = {
        "handle": value,
        "networkId": 19,
        "filterType": 28,
        "Latlng": latlng
    }
    // return new HttpClient().post(`${APP_API_URL}api/v1/feed/feeds/autocomplete`, { handle: searchQuery, networkId: 7, filterType: type });
    AUTOCOMPLETE(obj).then((response) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE(response?.data?.responseData ?? []))
    }).catch((error) => {
        dispatch(SET_AUTOCOMPLETE_HANDLE([]))
    })
}

export const RESET_AUTOCOMPLETE = () => (dispatch) => {
    dispatch(SET_AUTOCOMPLETE_HANDLE([]));
    dispatch(SET_AUTOCOMPLETE_LIST([]));
}

export const getFilterName = (filter) => {
    const { networkTab } = store.getState().addFeedReducer.networkDetails
    if (!networkTab) return
    return networkTab.find(tab => tab.filter == filter)?.feedName || "";
}


export const getFeedAlertText = (networkId) => (dispatch, getState) => {
    const { filter } = getState().addFeedReducer.feedObj
    if(networkId == 7 || networkId == 5 || networkId == 8 || networkId == 6 || networkId == 11) return "";

    switch (parseInt(filter)) {
        case 26:
            return "Instagram only allows posts from the last 24 hours. If there are no recent posts in the 24-hour duration, then the top 50 posts will be aggregated. Also, all your upcoming posts will be aggregated. Posts collected via hashtags will not include any information of the user."

        case 34:
            return "Initially stories from the last 24 hours will be shown after which all the upcoming stories will be aggregated."

        case 24:
            return "You will only get upcoming posts after creating mentions feed."

        case 25:
            return "Initially the latest 32 tagged posts on Instagram will be aggregated after which all the upcoming tagged posts will be aggregated."

        case 66:
            return "Initially 25 videos will be aggregated through handle after which all the upcoming videos will be aggregated."

        case 1:
            return [3, 20, 28].includes(parseInt(networkId)) ? "" : networkId == 1 ? "Initially the latest 100 posts on the Twitter handle will be aggregated after which all the upcoming posts will be aggregated." : "Initially 32 posts uploaded on personal accounts will be aggregated after which all the upcoming posts will be aggregated."

        case 2:
            return networkId == 20 ? "" : networkId == 3 ? "" : "Initially hashtag posts from the last 30 days will be aggregated after which all the upcoming posts will be aggregated."

        case 7:
            return "Initially mentions of the last 7 days will be aggregated after which all the upcoming mentions will be aggregated."

        case 33:
            return "Initially the latest 5 user reviews of Place will be aggregated after which all the upcoming reviews will be aggregated."

        case 29:
            return "Initially latest 50 user reviews will be aggregated after which all the upcoming reviews will be aggregated."

        case 3:
        case 4:
        case 5:
        case 23:
        default:
            return ""
    }
}

export const inputValidationByNetwork = (networkId) => (dispatch, getState) => {
    const { feedText, secondText, filter, field33placeId, feed33, field33placeName } = getState().addFeedReducer.feedObj
    let errorObj = { show: false, showSecond: false, message: "" };
    switch (parseInt(networkId)) {
        case 18:
            if ([26, 23].includes(parseInt(filter))) {
                if (feedText == "" || !feedText) errorObj = { show: true, message: "This field is required" }
                if (feedText[feedText.length - 1] == '@' || feedText[feedText.length - 1] == "#" || feedText.includes("@") || feedText.includes("#")) errorObj = { show: true, message: "@ and # not allowed" }
            }
            break;

        case 28:
            if (feedText == "" || !feedText) errorObj = { show: true, message: "This field is required" }
            break;

        case 1:
            if (feedText == "" || !feedText) errorObj = { show: true, message: "This field is required" }
            if (feedText[feedText.length - 1] == '@' || feedText[feedText.length - 1] == "#" || feedText.includes("@") || feedText.includes("#")) errorObj = { show: true, message: "@ and # not allowed" }
            break;

        case 4:
            if (filter == 29) return
            else if (!feed33 || !field33placeId || !field33placeName) errorObj = { show: true, message: "This field is required" }
            break;
        
        case 23:
            if (feedText == "" || !feedText) errorObj = { show: true, message: "This field is required" }
            else if(!URL_VALIDATION(feedText)) errorObj = { show: true, message: "Invalid URL" }
            break;
        
        case 15:
            if (feedText == "" || !feedText) errorObj = { show: true, message: "This field is required" }
            if (secondText == "" || !secondText) errorObj = {...errorObj, showSecond: true, message: "This field is required" }
            break;
        
        case 19:
            if (filter == 27 && (!URL_VALIDATION(feedText))) errorObj = { show: true, message: feedText?.length > 0 ? "Invalid URL" : "This field is required" }
            if(filter == 28) {
                if(feedText == "" || !feedText)  errorObj = { show: true, message: "This field is required" }
                if (secondText == "" || !secondText) errorObj = {...errorObj, showSecond: true, message: "This field is required" }
            }
            break;

        case 12:
            if (feedText == "" || !feedText) errorObj = { show: true, message: "This field is required" }
            if(!URL_VALIDATION(secondText)) errorObj = { ...errorObj, showSecond: true, message: secondText?.length > 0 ? "Invalid URL" : "This field is required" }
            break;

        case 10:
            if (filter == 16) {
                if (!URL_VALIDATION(feedText)) errorObj = { show: true, message: "Invalid URL" }    
            }
            break;

        case 5:
        case 7:
        case 6:
        case 8:
        case 3:
        case 11:
            if (networkId == 3 && (filter == 1 || filter == 62 || filter == 55 || filter == 66)) return
            if (feedText == "" || !feedText) errorObj = { show: true, message: "This field is required" }
            // if (feedText[feedText.length - 1] == '@' || feedText[feedText.length - 1] == "#" || feedText.includes("@") || feedText.includes("#")) errorObj = { show: true, message: "@ and # not allowed" }
            break;

        default:
            break;
    }

    if (errorObj.show || errorObj.showSecond) {
        dispatch(SET_ERROR_MESSAGE_ACITON(errorObj))
        return true
    }
    return false
}

export const getInputIcon = (filterId, networkId) => {
    switch (parseInt(filterId)) {
        case 26:
        case 72:
        case 2:
            return "hashtag"

        case 71:
        case 23:
        case 1:
        case 7:
        case 75:
        case 3:
        case 4:
            return (networkId === 7 && filterId === 4) ? "text" : "at"

        case 30: case 70: case 77: case 84: case 83: case 78: case 80:
            return "link"
        case 8:
            return "user"
        case 5:
            return "magnifying-glass"
        case 33:
            return "globe"
        case 12:
        case 56: case 16:
            return "flag"
        case 11:
            return "list"
        case 67:
            return "location-dot"
        case 22:
            return "users"
        case 13: case 27: case 28:
            return "font"

        default:
            return
    }
}

export const getInputPlaceholder = (filterData) => {
    switch (parseInt(filterData.filter)) {
        case 8:
            return "Enter Facebook Page URL"
        case 13:
            return "Enter RSS Display Name"
        case 27:
            return "Enter Bussiness Page URL"
        case 65:
            return "Enter album URL"
        case 67:
            return "Enter Facebook Page URL"
        default:
            return `Enter ${filterData?.feedtype || ""}`
    }
}